import { combineReducers } from 'redux';

const INITIAL_SEARCH = {
	available: false,
	success: false,
	activePage: 0,
}
const INITIAL_PARAMS = {
	submitted: false,
	ingrParams: "",
	dietParams: "",
	healthParams: [],
	calories: 500,
}
const searchReducer = (state = INITIAL_SEARCH, action) => {
	switch(action.type){
		case 'SEARCH':
			return {...state, Recipe:action.payload};
		case 'SUBMIT_SEARCH':
			return {...state, Recipes:action.payload, success: true, activePage: 1};
		case 'SEARCH_GIVEN':
			return {...state, available: action.payload, success: true, activePage: 1};
		case 'SUBMIT_PARAMS':
			return {...state, Recipes:action.payload, success: true, activePage: 1};
		case 'HANDLE_PAGE':
			return {...state, activePage: action.payload};
		case 'SUBMIT_FAIL':
			return {...state, success: false}
		default:
			return state;
	}
}
const paramReducer = (state = INITIAL_PARAMS, action) => {
	switch(action.type){
		case 'INGREDIENTS':
			return {...state, ingrParams: action.payload}
		case 'DIET':
			return {...state, dietParams: action.payload}
		case 'HEALTH':
			return {...state, healthParams: action.payload}
		case 'CALORIES':
			return {...state, calories: action.payload}
		case 'SUBMIT_FORM':
			return {...state, submitted: action.payload}
		default:
			return state;
	}
}
const bookmarkReducer = (state = [], action) => {
	switch(action.type){
		case 'BOOKMARK':
			return [...state, action.payload];
		case 'EXISTS':
		console.log('payload', action.payload);
			return state.filter(data=> {
				return data.recipe.label !== action.payload;
			})
		default:
			return state;
	}
}
export default combineReducers({
	search: searchReducer,
	params: paramReducer,
	bookmark: bookmarkReducer
})