import React from 'react';
import { Form, Button, Select} from 'semantic-ui-react';
import { dietOptions, healthOptions, ingrOptions } from '../data/dietLabels';
import { connect } from 'react-redux';
import {handleIngrParam, handleDietParam, handleHealthParam, searchTermGiven, submitForm, submitParams, handleCalories  } from '../actions';
class Accordion extends React.Component {
 	constructor(props){
 		super(props)
 		this.dummyRef = React.createRef();
 	}
 	renderSearchError = () => {
 		if(this.props.params.submitted && !this.props.searchTerm.available){
 			return (
				<div className="ui fluid red message">
				  <p>Pls enter a food to search before filtering</p>
				</div>
			);
 		}
 		if(this.props.params.submitted && this.props.searchTerm.available && !this.props.searchTerm.success){
 			return (
				<div className="ui fluid red message">
				  <p>No results found for such filter</p>
				</div>
			);
 		}
 		return null;
 	}
 	handleSubmit = (e,values) => {
 		this.props.submitForm(true)
 		if(this.props.searchTerm.available){
 			this.props.submitParams()
 		}
	}
	render(){
		const {handleIngrParam, handleDietParam, handleHealthParam, params, handleCalories } = this.props;
		return(
			<div>
				<Form ref={this.dummyRef} onSubmit={this.handleSubmit}>
				     <Form.Input
				        control={Select}
				        options={ingrOptions}
				        fluid
				        label={{ children: 'Max no. of Ingredients', htmlFor: 'form-select-control-ingredients' }}
				        placeholder='Max no. of ingredients'
				        search
				        searchInput={{ id: 'form-select-control-ingredients' }}
				        onChange={handleIngrParam}
				     />
				     <Form.Input
				        control={Select}
				        options={dietOptions}
				        fluid selection
				        label={{ children: 'Diet', htmlFor: 'form-select-control-diet' }}
				        placeholder='Diet Options'
				        search
				        searchInput={{ id: 'form-select-control-diet' }}
				        onChange={handleDietParam}
				     />
				     <Form.Input
				        control={Select}
				        options={healthOptions}
				        fluid multiple selection
				        label={{ children: 'Health', htmlFor: 'form-select-control-health' }}
				        placeholder='Health Options'
				        search
				        searchInput={{ id: 'form-select-control-health' }}
				        onChange={handleHealthParam}
				     />
					<Form.Input
			            label={`Calories: ${params.calories}cal `}
			            min={100}
			            max={2000}
			            name='calories'
			            onChange={handleCalories}
			            step={100}
			            type='range'
			            value={params.calories}
			          />
				    <Form.Field
				      id='form-button-control-public'
				      control={Button}
				      content='Filter'
				      label='Filter Results'
				    />
				</Form>
				{this.renderSearchError()}
			</div>
		);
	}
}
const mapStateToProps = state => {
	return {searchTerm: state.search,
		params: state.params
	}
}
export default connect(mapStateToProps,{handleIngrParam, handleDietParam, handleHealthParam, searchTermGiven, submitForm, submitParams, handleCalories })(Accordion);

