import history from '../history';

const key = 'a3068a044c623318afaa747afbf29331';
const id = 'eb17d189';
export const searchRecipe = value => {
	return {
		type: 'SEARCH',
		payload: value
	}
}
export const searchTermGiven = value => {
	return {
		type: 'SEARCH_GIVEN',
		payload: Boolean(value)
	}
}
export const submitSearch = value => async dispatch => {
	const response = await fetch(`https://api.edamam.com/search?q=${value}&app_id=${id}&app_key=${key}`)
	const obj = await response.json(); 
	dispatch({type: 'SUBMIT_SEARCH', payload: obj});
	history.push('/search');
}
export const handlePage = page => {
	return {
		type: 'HANDLE_PAGE',
		payload: page
	}
}
export const handleIngrParam = (e, metadata) => {
	return {
		type: 'INGREDIENTS',
		payload: metadata.value
	}
}
export const handleDietParam = (e, metadata) => {
	return {
		type: 'DIET',
		payload: metadata.value
	}
}
export const handleHealthParam = (e, metadata) => {
	return {
		type: 'HEALTH',
		payload: metadata.value
	}
}
export const handleCalories = (e, metadata) => {
	return {
		type: 'CALORIES',
		payload: metadata.value
	}
}
export const submitForm = submission => {
	return{
		type: 'SUBMIT_FORM',
		payload: submission
	}
}
export const submitParams = () => async (dispatch, getState) => {
	let query = "";
	const searchTerm = getState().search.Recipes.q;
	const {activePage} = getState().search;
	if(activePage){
		let finalRange = (activePage * 10);
		let initialRange = finalRange - 10;
		query = query + `&from=${initialRange}&to=${finalRange}`
	}
	const {ingrParams, dietParams, healthParams, calories} = getState().params;
	query = query + "&q=" + searchTerm;
	if(ingrParams){
		query = query +"&ingr="+ ingrParams;
	}
	if(dietParams){
		query = query + "&diet=" + dietParams;
	}
	if(healthParams[0]){
		healthParams.forEach(param => {
			query = query + "&health=" + param;
		})
	}
	if(calories){
		query = query + "&calories=" + calories + "%2B";
	}
	console.log(query)
	try{
	const response = await fetch(`https://api.edamam.com/search?app_id=${id}&app_key=${key}` + query)
	const obj = await response.json();
	dispatch({type: 'SUBMIT_PARAMS', payload: obj});
	}catch(err){
		dispatch({type: 'SUBMIT_FAIL'})
	} 
}
export const bookmark = (info) => async (dispatch,getState)=>{
	if(getState().bookmark.length){
		const exists = getState().bookmark.filter(data=>{
			return data.recipe.label === info.recipe.label;
		})
		if(exists.length){
			dispatch({type: 'EXISTS', payload: info.recipe.label});
			return;
		}
	}
	dispatch({type: 'BOOKMARK', payload: info});
}