import React from 'react';
import { Tab } from 'semantic-ui-react';

const panes = [
  { menuItem: 'Ingredients', render: (props) => {
  	return (
  	<Tab.Pane attached={false}>
  		<div className="ui items">
				<div className="item">
					<div className="image">
						 <img src={props.info.recipe.image} alt="Food"/>
					</div>
					    <div className="content">
					      <a href={props.info.recipe.url}className="header" target="_blank" rel="noopener noreferrer">{props.info.recipe.label}</a>
					    <div className="meta">
					    	<button class="ui instagram button" style={{margin: '5px 0'}} onClick={() => props.bookmark(props.info)}>
							  <i class="bookmark icon"></i>
							  Bookmark
							</button>
					        <span>{props.info.recipe.ingredientLines.map(line=> {
					        	return (
					        		<p>{line}</p>
					        	);
					        })}</span>
					    </div>
					    </div>
				</div>
			</div>
  	</Tab.Pane>) }
  },
  { menuItem: 'Nutrients', render: (props) => {
  		return (
  		<Tab.Pane attached={false}>
  			<div className="ui items">
					<div className="item">
						<div className="image">
						   <img src={props.info.recipe.image} alt="Food"/>
						</div>
	  			<div className="content">
					<a href={props.info.recipe.url}className="header" target="_blank" rel="noopener noreferrer">{props.info.recipe.label}</a>
					    <div className="meta">
					       <span>{Object.keys(props.info.recipe.totalNutrients).map(key=>{
					       		return (
					       			<p>{`${props.info.recipe.totalNutrients[key].label}: ${Math.round(props.info.recipe.totalNutrients[key].quantity)} ${props.info.recipe.totalNutrients[key].unit}`}</p>
					       		);
					       })}</span>
					    </div>
				</div>
				</div>
			</div>
  		</Tab.Pane>
  		)
  	}
 }
]

class RecipeCard extends React.Component {
	render(){
		const {info, bookmark} = this.props;
		return (
			<Tab menu={{ secondary: true, pointing: true }} panes={panes} info={info} key={info.recipe.label} bookmark={bookmark}/>
		);
	};
};
export default RecipeCard;