import React from 'react';
import {connect } from 'react-redux';
import RecipeCard from './RecipeCard';
import { bookmark } from '../actions';

class Bookmark extends React.Component{
	renderList(){
		if(!this.props.bookmarks.length){
			return (
				<h2 class="ui center aligned icon header">
				  <i class="bookmark outline icon"></i>
				  <div class="content">
				    Bookmarks
				    <div class="sub header">Look up recipes and come back here when you have some bookmarked.</div>
				  </div>
			</h2>
			);
		}
		return this.props.bookmarks.map(info => {
			return (
				<RecipeCard info={info} bookmark={this.props.bookmark} key={info.recipe.label}/>
			);
		})
	}
	render(){
	return (
		<div>
			{this.renderList()}
		</div>
	);
	};
};
const mapStateToProps = state => {
	return {bookmarks: state.bookmark};
}
export default connect(mapStateToProps, {bookmark})(Bookmark);