import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import SearchBar from './SearchBar';
import {searchRecipe, submitSearch, searchTermGiven, submitForm} from '../actions';
class SideMenu extends React.Component {
  render() {
    return (
      <div className="ui inverted menu">
		  <Link to="/mealtime" className="active red item">
				<i class="home icon"></i>
		  </Link>
		  <Link to="/search" className="active green item">
				<i class="utensils icon"></i>
		  </Link>
		  <Link to="/bookmarks" className="active blue item">
				<i class="bookmark icon"></i>
		  </Link>
		    <SearchBar/>
		</div>

    )
  }
}

const mapStateToProps = (state) => {
	return {searchTerm: state.search}
}
export default connect(mapStateToProps,{searchRecipe, submitSearch, searchTermGiven, submitForm})(SideMenu);